$topHeaderHeight        : 66px;
$railNavWidth           : 66px;
$categoryContainerWidth : 344px;
$expandedNavOffset      : 184px;

#catalog-list-container {

	#category-container {
		background: $app-sidebar-bg;
		position: fixed;
		height: calc(100vh - #{$topHeaderHeight});
		overflow-y: auto;
		width: $categoryContainerWidth;
		padding: 16px;

		.shimmer-loader-item-fast.line {
			margin-bottom: 24px;
			float: none;
		}

		.category-item {
			display: flex;
			align-items: center;
			border-radius: $radius;
			padding: 16px;
			font-size: 14px;
			color: $primary-text-color-1;
			font-weight: 600;
			margin: 2px 0;
			justify-content: space-between;

			&.active {
				background: $app-white;
				border-left: 4px solid $app-primary;
				box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.0293925);
				padding-right: 8px;
				transition: 0.15s ease-in-out;
				margin-left: -4px;

				svg.chevron-right {
					fill: $primary-text-color-1;
				}

				&:focus-visible {
					box-shadow: 0 0 0 2px $linkColor;
				}
			}

			&:not(.active):hover {
				background: $app-border-primary;
			}
		}
	}

	.page-content.main-container {
		position: absolute;
		background: $app-white;
		height: calc(100vh - #{$topHeaderHeight});
		width: calc(100vw - #{$railNavWidth + $categoryContainerWidth});
		margin: 0;
		right: 0;
		border-radius: 0;
		overflow-y: auto;
		overflow-x: hidden;
		box-sizing: border-box;
		padding-bottom: 16px;
		display: flex;
		justify-content: center;

		@import 'list-empty-state';

		.search-container {
			width: calc(100vw - 464px);
			height: 128px;
			box-sizing: border-box;
			backdrop-filter: blur(5px);
			background-color: rgba(255,255,255,.7);
			display: flex;
			justify-content: center;
			padding: 32px 4px 0 4px;
			right: 28px;
			z-index: 2;

			position: fixed;
			top: 60px;

			.search-input-wrapper {
				width: 100%;
				height: 96px;
				background: $app-grey-bg;
				padding: 24px 32px;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 4px;
				position: relative;
				max-width: 968px;
				border-color: none;

				&, &.active {
					border-color: transparent;
					box-shadow: none;
				}

				input, button {
					width: 100%;
					height: 48px;
				}

				.search-icon {
					position: absolute;
					left: 46px;
					fill: $secondary-text-color-1;
					width: 18px;
					height: 18px;
				}

				input.search-items {
					padding: 6px 40px;
					font-size: $title-normal;
					font-family: $baseFontFamily;

					/// Using !important to override styles which already have !important in _debounced-search-input.scss 
					&:focus {
						border: 1px solid $inputFocusBorder !important;
						box-shadow: 0 0 0 1px $inputFocusBorder !important;
					}
				}

				.search-clear {
					position: absolute;
					width: 32px;
					height: 32px;
					right: 40px;
					padding: 0;
					border: 0;

					svg.close-icon {
						fill: $secondary-text-color-1;
						width: 32px;
						height: 32px;
					}
				}

				.shimmer-loader-item-fast {
					width: 100%;
					height: 52px;
				}
			}

		}

		.list-container {
			padding: 32px;
			padding-top: 160px;
			z-index: 1;

			.title-normal {
				margin-bottom: 16px;
			}
		}
	}

	.page-layout-content .page-sidebar.category-list {
		width: $categoryContainerWidth;
		min-height: calc(100vh - 66px);
	}
	@import "item-list";
}

@media (width: 1920px) {
	#catalog-list-container {

		.main-container {
			display: flex;
			justify-content: center;

			.search-container .search-input-wrapper {
				width: 1366px;
				max-width: none;
			}

			.list-container {
				width: 1430px;
			}

			.list-catalog-items {
				grid-template-columns: 329px 329px 329px 329px;
			}
		}
	}
}

@media (min-width: 1921px) {
	@at-root body[data-nav-expanded="true"] #ember-body-content.ember-application #catalog-list-container {
		.page-content.main-container {
			width: calc(100vw - #{$railNavWidth + $categoryContainerWidth + $expandedNavOffset});
		}
		.search-container {
			width: calc(100vw - #{$railNavWidth + $categoryContainerWidth + $expandedNavOffset} - 56px) !important;
		}
	}
}